import { useRouter, useRoute } from 'vue-router';
import { reactive } from 'vue';
export default {
  name: 'TabBar',
  props: ['items'],

  setup(props, context) {
    const router = useRouter();
    const route = reactive(useRoute());

    const clickEvent = item => {
      router.push({
        name: item.name
      });
    };

    return {
      route,
      clickEvent
    };
  }

};