import TabBar from '@/components/TabBar';
import { useRoute, useRouter } from 'vue-router';
import { reactive, ref } from 'vue';
import cache from '@/common/cache';
export default {
  name: 'TabBarController',
  components: {
    TabBar
  },

  setup() {
    const route = reactive(useRoute());
    const itemList = ref([]);
    const routes = useRouter();
    const children = routes.options.routes.find(e => {
      return e.name === 'tabBar';
    }).children;
    itemList.value = children.filter(e => {
      const appType = cache.value('appType');
      return appType === 'user' ? e.meta.isUserSide : !e.meta.isUserSide;
    });
    return {
      route,
      itemList
    };
  }

};